.page.dash .addingCatwalk {
  padding: 0.25em 1em 1em 1em;
  background-color: #3f51b5;
}
.page.dash .addingCatwalk.ng-enter {
  -webkit-animation: fadeInDown 1s;
  animation: fadeInDown 1s;
}
.page.dash .addingCatwalk.ng-leave {
  -webkit-animation: fadeOutUp 0.5s;
  animation: fadeOutUp 0.5s;
}
.page.dash .onboarding {
  color: #fff;
}
.page.dash .fields {
  padding: 0em 0.5em;
  background-color: #fff;
}
.page.dash md-list h3,
.page.dash md-list p {
  margin: 0;
  padding: 0;
}
.page.dash md-list p {
  color: #999;
}
.page.dash md-list .details {
  margin-left: 4em;
}
.page.dash .loadspinner {
  font-size: 2em;
  color: #999;
  text-align: center;
  padding: 2em 0;
}
.page.dash .loadspinner md-progress-circular {
  margin: 1em auto;
}
.page.splash h1 {
  text-align: center;
}
.page.splash .promise {
  padding-top: 250px;
  background-image: url("../assets/images/cat.png");
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover;
}
.page.splash .promise .caption {
  padding: 1em;
  background-color: #283593;
  color: #fff;
  font-size: 1.5em;
  text-align: center;
}

